import React from "react";
import styled from "styled-components";
import { withRouter } from "react-router";
import axios from "axios";
import Song from "./components/Song";
import Artist from "./components/Artist";
import Album from "./components/Album";

const URL = "https://deezerdevs-deezer.p.rapidapi.com/search?";

class Result extends React.Component {
  constructor(props){
    super(props);
    this.gotoSong=React.createRef();
    this.gotoArtist=React.createRef();
    this.gotoAlbum=React.createRef();
    this.state ={
      id: props.match.params.id,
      datas: ""
    };
  }
  async componentDidMount() {
    const {
      data: { data }
    } = await axios.get(`${URL}`, {
      params: {
        q: this.state.id,
        limit: 50
      },
      method: "GET",
      headers: {
        "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
        "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
      }
    });
    this.setState({
      datas: data
    });
    const newMapAR = data.map(each => each.artist.id);
    const settedAR = new Set(newMapAR);
    const arrayedAR = Array.from(settedAR)
    for(let i=0; i<arrayedAR.length; i++){
      for(let j=0; j<data.length; j++){
        if(data[j].artist.id === arrayedAR[i]){
            arrayedAR[i]=data[j].artist;
            break;
        }
      }
    }

    const newMapAB = data.map(each => each.album.id);
    const settedAB = new Set(newMapAB);
    const arrayedAB = Array.from(settedAB)
    for(let i=0; i<arrayedAB.length; i++){
      for(let j=0; j<data.length; j++){
        if(data[j].album.id === arrayedAB[i]){
            arrayedAB[i]=data[j].album;
            break;
        }
      }
    }
    this.setState({
      artists : arrayedAR,
      albums : arrayedAB
    })
    
  }
  check() {
    if (this.state.id !== this.props.match.params.id) {
      window.location.reload();
    }
  }
  topHandler=(e)=>{
    window.scrollTo(0,0)
  }
  songHandler=(e)=>{
   this.gotoSong.current.scrollIntoView();
  }
  artistHandler = (e)=>{
    this.gotoArtist.current.scrollIntoView();
  }
  albumHandler = (e) =>{
    this.gotoAlbum.current.scrollIntoView();
  }
  render() {
    this.check();
    const {
      state: { datas }
    } = this;

    return (
      <Container>
        <Navigator>
        <Button onClick={this.topHandler}>TOP</Button>  
        <Button onClick={this.songHandler}>BY SONGS</Button>
        <Button onClick={this.artistHandler}>BY Artists</Button>
        <Button onClick={this.albumHandler}>BY Albums</Button>
        </Navigator>
        <Sort ref={this.gotoSong}>BY SONGS</Sort>
        <Songs>
          {datas
            ? datas.map((each, index) => (
                <Song
                  key={index}
                  id={index}
                  title={each.title}
                  preview={each.preview}
                  artist={each.artist}
                ></Song>
              ))
            : null}
        </Songs>
        <Sort ref={this.gotoArtist}>BY ARTISTS</Sort>
        <Artists>
          {this.state.artists
            ? this.state.artists.map((each, index) => (
                <Artist key={index} id={index} artist={each}></Artist>
              ))
            : null}
        </Artists>
        <Sort ref={this.gotoAlbum}>BY ALBUMS</Sort>
        <Albums>
          {this.state.albums
            ? this.state.albums.map((each, index) => (
                <Album key={index} id={index} album={each}></Album>
              ))
            : null}
        </Albums>
      </Container>
    );
  }
}

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
`;
const Navigator = styled.div`
/* background-color: rgba(250, 250, 250, 0.85); */
height:2.5rem;
width:100%;
position:-webkit-sticky;
position:sticky;
top:20px;
display:flex;
justify-content:center;
align-items:center;
padding-top:50px;
`;
const Button = styled.button`
background-color:white;
border-radius:20px;
margin:10px;
padding:0 1rem;
border: 2px solid rgba(24, 21, 50, 0.55);
height:2rem;
outline:0;
cursor: pointer;
`;
const Songs = styled.div`
  width: 80%;
  display:grid;
  grid-template-columns:1fr 1fr;
  grid-gap:20px;
`;
const Albums = styled.div`
  width: 80%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap:30px;
  padding:30px 0;
`;
const Artists = styled.div`
  width: 80%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap:30px;
  padding:30px 0;
`;
const Sort = styled.h1`
  padding-top: 130px;
  padding-bottom: 20px;
  width: 80%;
`;
export default withRouter(Result);
