import React from 'react'
import styled from 'styled-components'
import axios from 'axios';
import {Link} from 'react-router-dom'
async function getDatas(url) {
  const proxyurl = "https://cors-anywhere.herokuapp.com/";
  const {data:{data}} = await axios.get(`${proxyurl}${url}?`,{
      params:{
          limit:10
      },
      method:'GET',
      headers:{
          "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
          "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
      }
  });
  return data;
}

export default ({id, album})=>{
      let result='';
      let datas=getDatas(album.tracklist)
      datas.then(res=>console.log('id', id, res));
      
  return (
    <Contaier>
      <Poster poster={album.cover_medium}></Poster>
      <Link to={`/album/${album.title}`}>
      <Name>{album.title}</Name>
      </Link>
    </Contaier>
  );
};

const Contaier = styled.div`
  display:grid;
  grid-template-columns:1fr 2fr;
  margin:0 30px;
`;
const Name = styled.span`
align-self:center;
padding:10px;
`;
const Poster = styled.div`
  background-image: url(${props => props.poster});
  background-position:center center;
  background-repeat:no-repeat;
  background-size:cover;
  width: 150px;
  height: 150px;
`;
